.experience {
  width: 100%;
  max-width: 90%;
  margin: 0 auto;
  background: -webkit-linear-gradient(307deg, #621e8f 16%, #762ca6 56%, #cf6e88 99%);
  background: -o-linear-gradient(307deg, #621e8f 16%, #762ca6 56%, #cf6e88 99%);
  background: linear-gradient(-217deg, #621e8f 16%, #762ca6 56%, #cf6e88 99%);
  border-radius: 30px;
}

.experience__text {
  color: white;
  padding-top: 50px;
  font-size: 40px;
  line-height: 1.05;
  font-weight: 700;
  letter-spacing: -0.015em;
  text-align: center;
}



