.header {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  /* background-color: #1d1d1f; */
  /* background-color: #111614	; */
  padding: 18px 0;
}

.header__container {
  width: 89.0625%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
}

.header__logo {
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.header__logo:hover {
  opacity: 0.8;
  -webkit-transition: ease 1s;
  -o-transition: ease 1s;
  transition: ease 1s;
}

@media all and (max-width: 800px) {
  .header__container {
    width: 92.1875%;
  }
}



